import { Component, NgModule, OnInit, ViewEncapsulation } from '@angular/core';
import { CommonModule } from '@angular/common';
import { OSharedModule } from '../../../shared';
import { InputConverter } from '../../../decorators';
import { DEFAULT_INPUTS_O_REAL_INPUT, DEFAULT_OUTPUTS_O_REAL_INPUT, ORealInputComponent, ORealInputModule } from '../real-input/o-real-input.component';

export const DEFAULT_INPUTS_O_PERCENT_INPUT = [
  ...DEFAULT_INPUTS_O_REAL_INPUT
];

export const DEFAULT_OUTPUTS_O_PERCENT_INPUT = [
  ...DEFAULT_OUTPUTS_O_REAL_INPUT
];

@Component({
  moduleId: module.id,
  selector: 'o-percent-input',
  template: `
    <div [formGroup]="getFormGroup()" [matTooltip]="tooltip" [matTooltipClass]="tooltipClass"
      [matTooltipPosition]="tooltipPosition" [matTooltipShowDelay]="tooltipShowDelay"
      [matTooltipHideDelay]="tooltipHideDelay" fxLayout="row" fxLayoutAlign="space-between center">
      <mat-form-field [appearance]="appearance" [floatLabel]="floatLabel" [hideRequiredMarker]="hideRequiredMarker"
        [class.custom-width]="hasCustomWidth" class="icon-field" fxFlexFill>
        <mat-label *ngIf="labelVisible">{{ olabel | oTranslate }}</mat-label>
        <input matInput [type]="inputType" [id]="getAttribute()" [formControlName]="getAttribute()"
          [placeholder]="placeHolder" (focus)="innerOnFocus($event)" (blur)="innerOnBlur($event)" [readonly]="isReadOnly"
          (change)="onChangeEvent($event)" [min]="min" [max]="max" [step]="step" [required]="isRequired">
        <button type="button" *ngIf="showClearButton" matSuffix mat-icon-button (click)="onClickClearValue($event)">
          <mat-icon svgIcon="ontimize:close"></mat-icon>
        </button>
        <mat-icon svgIcon="ontimize:PERCENT" matSuffix class="svg-icon" [class.mat-disabled]="!enabled"></mat-icon>
        <mat-error *ngIf="hasError('required')" text="{{ 'FORM_VALIDATION.REQUIRED' | oTranslate }}"></mat-error>
        <mat-error *ngIf="hasError('min')"
          text="{{ 'FORM_VALIDATION.MIN_VALUE' | oTranslate }}: {{ getErrorValue('min', 'requiredMin') }}"></mat-error>
        <mat-error *ngIf="hasError('max')"
          text="{{ 'FORM_VALIDATION.MAX_VALUE' | oTranslate }}: {{ getErrorValue('max', 'requiredMax') }}"></mat-error>
        <mat-error *ngIf="hasError('minDecimaldigits')"
          text="{{ 'FORM_VALIDATION.MIN_DECIMAL_DIGITS' | oTranslate }}: {{ getErrorValue('minDecimaldigits', 'requiredMinDecimaldigits') }}">
        </mat-error>
        <mat-error *ngIf="hasError('maxDecimaldigits')"
          text="{{ 'FORM_VALIDATION.MAX_DECIMAL_DIGITS' | oTranslate }}: {{ getErrorValue('maxDecimaldigits', 'requiredMaxDecimaldigits') }}">
        </mat-error>
        <mat-error *ngFor="let oError of getActiveOErrors()" text="{{ oError.text | oTranslate }}"></mat-error>
      </mat-form-field>
    </div>
  `,
  styles: [`

  `],
  inputs: DEFAULT_INPUTS_O_PERCENT_INPUT,
  outputs: DEFAULT_OUTPUTS_O_PERCENT_INPUT,
  encapsulation: ViewEncapsulation.None
})
export class OPercentInputComponent extends ORealInputComponent implements OnInit {

  public static DEFAULT_INPUTS_O_PERCENT_INPUT = DEFAULT_INPUTS_O_PERCENT_INPUT;
  public static DEFAULT_OUTPUTS_O_PERCENT_INPUT = DEFAULT_OUTPUTS_O_PERCENT_INPUT;

  @InputConverter()
  grouping: boolean = true;

  public ngOnInit() {
    if (typeof (this.min) === 'undefined') {
      this.min = 0;
    }
    if (typeof (this.max) === 'undefined') {
      this.max = 100;
    }
    super.ngOnInit();
  }
}

@NgModule({
  declarations: [OPercentInputComponent],
  imports: [CommonModule, OSharedModule, ORealInputModule],
  exports: [OPercentInputComponent, ORealInputModule]
})
export class OPercentInputModule { }
